/*==resource/inventory depot styles==*/
/*==resource tally style==*/
.kdr-tally-element
{
	text-align: center;
	display: inline-block;
	padding-right: 5px;
	
}
.kdr-tally-display
{
	border: 2px solid white;
	border-radius: 5px;
	font-size:20px;
	padding: 10px;
	width: 45px;
}

.kdr-tally-label
{
	font-weight: bold;
}

/*==resource group style==*/
.kdr-formgroup
{
	border-bottom: 1px solid white;
	padding-bottom: 5px;
}

.kdr-formgroup label
{
	position: relative;
	top: 7px;
	padding: 0px 0px 0px 5px;
}

.kdr-panel .panel-heading
{
	background-color: #b38f00;
	border-color: #b38f00;
}

.kdr-panel .panel-title
{
	color: white;
}

.kdr-panel
{
	background-color: #b38f00;
	border-color: #b38f00;
}

/*==resource style==*/
.kdr-inputgroup
{
	width:100px;
}


