/*===========================================================*/
html, body
{
    height: 100%;
	padding: 0px;
	margin: 0px;
	
	color: white;
	background-color: black;
	background-image: url('../images/cardback.png');
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center center;
	background-attachment: fixed;
}

/*kdapp*/
.kdapp_page_title
{
    font-weight: bold;
}

.kdapp_logo
{
	height: 40px;
	position: relative;
	bottom: 10px;
	right: 5px;
}

.kdapp_title
{
	position:relative;
	bottom: 7px;
}

.kdapp_page
{
	padding: 0px 10px;
}

hr
{
	margin-top: 10px;
	margin-bottom: 10px;
	border: 0;
	border-top: 1px solid white;
}

/*kdh*/
.kdh
{
	font-weight: bold;
	text-align: center;
}


